<template>
  <div style="width:100%">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal">
      <el-menu-item style="cursor:default">
        <img draggable="false" style="height:100%;" src="../assets/image/logo.png" />
      </el-menu-item>
      <el-menu-item index="/">
        <li @click="toFront">首页</li>
      </el-menu-item>
      <transition name="el-fade-in-linear">
        <el-menu-item v-if="loginState" index="/task">
          <li @click="toTaskList">任务列表</li>
        </el-menu-item>
      </transition>
      <transition name="el-fade-in-linear">
        <el-menu-item v-if="!loginState" style="float:right">
          <el-button type="primary" round @click="login">登录</el-button>
          <el-button type="primary" round @click="register">注册</el-button>
        </el-menu-item>
      </transition>
      <transition name="el-fade-in-linear">
        <el-menu-item v-if="loginState">
          <span class="el-icon-user-solid user-item">
            欢迎您:
            <span class="username-item">{{userName}}</span>
          </span>
        </el-menu-item>
      </transition>
      <transition name="el-fade-in-linear">
        <el-menu-item v-if="loginState" style="float:right">
          <el-button type="primary" round @click="exit">退出</el-button>
        </el-menu-item>
      </transition>
    </el-menu>

    <!-- <router-link to="/">Home</router-link> |
    <router-link to="/about">About</router-link>-->
    <div class="main">
      <div style="margin-top:60px">
        <router-view />
      </div>
    </div>

    <div v-if="!taskPage" style="margin-top:80px">
      <h1 style="display:inline-block;margin-right:2em;">动漫大师</h1>
