          @drop="dashBoardDrop"
        >
          <h2>上传文件，开始你的动画制作 ！</h2>
          <div class="board-icon file">
            <!-- <i class="el-icon-upload" style="font-size:5em;cursor: pointer;" @click="openLoginTips"></i> -->
            <svg
