              accept="image/png, image/jpeg, audio/mp3, audio/mp4, video/mp4"
              @change="openDialog"
            />
          </div>
          <div class="head-container">
            <p class="board-head">拖拽文件至此或者点击上方图标</p>
          </div>
        </div>
      </div>
    </div>
    <!-- Form -->
    <el-dialog
      :title="fileTitle"
      :visible.sync="dialogFormVisible"
      :before-close="handleClose"
      center
      style="z-index:1000"
    >
      <el-steps :active="stepActive" finish-status="success" align-center>
        <el-step title="选取文件"></el-step>
        <el-step title="选择文件类型"></el-step>
        <el-step title="选取风格并点击上传"></el-step>
        <el-step title="等待后台处理..."></el-step>
        <i v-if="canBackLast">
          <el-tooltip class="item" effect="dark" content="选错风格可以回到上一步重新选择" placement="top">
