              <path d="M204.8 0h76.15488L378.88 112.64h-76.16512z" fill="#FFFFFF" p-id="8232" />
              <path d="M389.12 0h76.15488L563.2 112.64h-76.16512z" fill="#FFFFFF" p-id="8233" />
              <path d="M573.44 0h76.15488L747.52 112.64h-76.16512z" fill="#FFFFFF" p-id="8234" />
              <path d="M757.76 0h76.15488L931.84 112.64h-76.16512z" fill="#FFFFFF" p-id="8235" />
              <path d="M204.8 225.28h76.15488L378.88 112.64h-76.16512z" fill="#FFFFFF" p-id="8236" />
              <path d="M389.12 225.28h76.15488L563.2 112.64h-76.16512z" fill="#FFFFFF" p-id="8237" />
              <path
                d="M573.44 225.28h76.15488L747.52 112.64h-76.16512z"
