      </transition>
      <el-collapse-transition>
        <el-form :model="form" v-if="hasType" style="margin-top:2em">
          <el-form-item v-show="imageOption" label="图像风格" :label-width="formLabelWidth">
            <el-select
              @change="esTime"
              v-model="form.imageValue"
              placeholder="请选择图像风格"
              value-key="ims_id"
            >
              <el-option
                style="height:5.5em"
