                  style="float: right; color: #8492a6; font-size: 13px"
                >{{ item.ims_description }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="audioOption" label="音频风格" :label-width="formLabelWidth">
            <el-select
              @change="esTime"
              v-model="form.audioValue"
              placeholder="请选择音频风格"
              value-key="aus_id"
            >
              <el-option
                style="height:5.5em"
                v-for="item in styleResultData.data.audio"
                :key="item.aus_id"
                :label="item.aus_name"
                :value="item"
              >
                <span>{{ item.aus_name }}</span>
                <!-- TODO:这里要换成插入音频 -->
