                  style="float: right; color: #8492a6; font-size: 13px"
                >{{ item.aus_description }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="clarityOption" label="清晰度" :label-width="formLabelWidth">
            <el-select v-model="form.clarityValue" placeholder="请选择清晰度">
              <el-option
                v-for="item in styleResultData.data.clarity"
                :key="item"
                :label="item"
                :value="item"
              >
                <span>{{ item }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="isFrameOption" label="降低效果以加速" :label-width="formLabelWidth">
            <el-switch @change="esTime" v-model="fileInformation.isFrame"></el-switch>
          </el-form-item>
        </el-form>
      </el-collapse-transition>
      <!-- <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>-->
      <div v-if="hasType" style="text-align:center">
        <el-button type="primary" @click="createTask">
          确定上传
          <el-tooltip class="item" effect="dark" content="预估的后台需要的处理时间" placement="bottom-start">
            <div style="margin-top:1em">
              <i>预计时间：</i>
              <i>{{Number.isNaN(formatDuration(fileInformation.estimated_time))?'--：--：--':formatDuration(fileInformation.estimated_time)}}</i>
            </div>
          </el-tooltip>
