<script>
import $ from "jquery";
// import ShowImage from "@/components/ShowImage.vue";
export default {
  //路由回来时候为true？
  props: {
    // loginState: Boolean
  },
  data() {
    return {
      name: "main",
      userId: this.$route.params.userId,
      userName: this.$route.params.userName,
      activeIndex: "/",
      activeIndex2: "/task",
      //loginState: this.$parent.loginState, //只能传静态的值
      loginState: false,
      taskPage: false,

      //对话框显示与否TODO:
      dialogFormVisible: false,
      form: {
        imageValue: {},
        audioValue: {},
        clarityValue: 720
      },
      formLabelWidth: "120px",

      //接收风格列表请求返回的数据：
      styleResultData: {
        data: {}
      },
      // 步骤条
      stepActive: 1,
      canBackLast: false, //返回上一步的按钮的显示
      hasType: false, // 是否已经选取了style
      fileTitle: "文件名:", //文件对话框的文件名显示
      fileInformation: {
        //上传文件时需要的文件信息
        name: "",
        type: "", //获取到的文件类型，用于判断
        file: {},
        height: 1,
        width: 1,
        duration: 0,
        styleString: "", //创建任务时的要发送的文件类型
        isFrame: false, //是否补帧加速
        estimated_time: 0
      },
      //按钮的禁用
      notImage: false,
      notAudio: false,
      notVideo: false,
      //控制select的显示
      imageOption: false,
      audioOption: false,
      clarityOption: false,
      isFrameOption: false
    };
  },
  created: function() {
    this.isLogin();
  },
  methods: {
    //判定是否处于登录状态
    isLogin: function() {
      console.log("调用了isLogin函数");
      //TODO:登陆成功才能改值
      // console.log(this.name);
      $.ajax({
        type: "POST",
        url: "/user/getSession"
      }).done(resultData => {
        // let dataObj = JSON.parse(resultData);
        let dataObj = resultData;
        let error_code = dataObj.error_code;
        let self = this;
        switch (error_code) {
          case 0:
            if (dataObj.data !== "") {
              console.log(true);
              //TODO:修改userName
              self.loginState = true;
              self.userId = dataObj.data.userId;
              self.userId = dataObj.data.userName;
              // return true;
            } else this.loginState = false;
        }
      });
    },
    //登录按钮
    login: function() {
      // console.log("点击了登录");
      // this.isLogin();
      //指定跳转地址
      this.$router.replace("/login");
      // this.loginState = true;
    },
    //注册按钮
    register: function() {
      console.log("点击了注册");
      this.$router.replace("/register");
    },
    // 前往任务列表
    toTaskList: function() {
      this.$router.push({
        name: "task"
        // params: { username: "liu", userId: 12 }
      });
      this.taskPage = true;
    },
    //返回首页
    toFront: function() {
      this.$router.push({
        name: "main"
        // params: { username: "liu", userId: 12 }
      });
      this.taskPage = false;
    },
    //退出登录 按钮
    exit: function() {
      // TODO:
      this.loginState = false;
    },

    //未登录状态下点击图片
    openLoginTips: function() {
      //在未登录状态下弹出对话框提示登录
      // this.$router()
      // console.log(this);
      let self = this;
      if (!this.loginState) {
        this.$swal({
          // toast: true,
          // position: 'top',
          showConfirmButton: true, //确定按钮
          allowOutsideClick: false, //点击背景不会关闭
          timer: 3000,
          type: "warning",
          title: "您尚未登陆",
          onClose: function() {
            //弹窗关闭时调用的函数
            // showModal("login-box");
            self.$router.push({ name: "login" });
          }
        });
      }
    },
    // 返回上一步
    backToLast: function() {
      this.stepActive--;
      this.hasType = false;
      this.canBackLast = false;
      // if (this.stepActive < 2) this.stepActive = 2;
    },

    // 上传文件后触发事件（onchange）
    openDialog: function() {
      //为input绑定事件
      let self = this;
      let file = this.$refs.fileUpload.files[0];
      //如果不合格式，重新选文件
      if (
        file.type !== "image/png" &&
        file.type !== "image/jpeg" &&
        file.type !== "audio/mp3" &&
        file.type !== "audio/mp4" &&
        file.type !== "video/mp4"
      ) {
        self.$swal({
          // toast: true,
          // position: 'top',
          showConfirmButton: true,
          allowOutsideClick: false, //点击背景不会关闭
          type: "warning",
          title: "不支持该格式",
          text: "仅支持mp4，jpg，jpeg，png，mp3,请重新选择"
        });
      } else if (file.size > 400 * 1000 * 1000) {
        self.$swal({
          // toast: true,
          // position: 'top',
          showConfirmButton: true,
          allowOutsideClick: false, //点击背景不会关闭
          type: "warning",
          title: "文件太大了",
          text: "请尝试上传小于400M的文件"
        });
      }
      //符合要求的格式
      else {
        self.fileInformation.name = file.name;
        self.fileInformation.type = file.type;
        self.fileInformation.uploadFile = file;
        console.log(self.fileInformation);
        //"image/png,image/jpeg,audio/mpeg,audio/mp4, video/mp4"
        //传的是图片
        if (file.type === "image/png" || file.type === "image/jpeg") {
          let reader = new FileReader();
          reader.readAsDataURL(file); //这里把一个文件用base64编码
          reader.onload = function(e) {
            let img = new Image();
            img.src = e.target.result; //获取编码后的值,也可以用this.result获取
            img.onload = function() {
              console.log("height:" + this.height + "----width:" + this.width);
              // document.getElementById("uploadFilename").innerHTML =
              //   fileInformation.name;
              self.fileInformation.height = this.height;
              self.fileInformation.width = this.width;
              self.fileInformation.duration = 0;
              self.notAudio = true;
              self.notVideo = true;
              self.dialogFormVisible = true;
            };
          };
        }
        //传的是视频
        else if (file.type === "video/mp4") {
          //等待视频数据加载弹框
          self.$swal({
            // toast: true,
            // position: 'top',
            showConfirmButton: false,
            allowOutsideClick: false, //点击背景不会关闭
            type: "info",
            title: "<i class='el-icon-loading'></i>",
            text: "文件读取中..."
          });

          let reader = new FileReader();
          reader.readAsDataURL(file); //这里把一个文件用base64编码
          reader.onload = function(e) {
            let dataUrl = reader.result;
            let videoId = "videoMain";
            let $videoEl = $('<video id="' + videoId + '"></video>');
            // $("body").append($videoEl);
            $videoEl.attr("src", dataUrl);
            let videoTagRef = $videoEl[0];

            //媒体（音频、视频）相关事件loadstart、loadmetadata
            //参考https://developer.mozilla.org/zh-CN/docs/Web/Guide/Events/Media_events

            videoTagRef.addEventListener("loadedmetadata", function(e) {
              console.log(
                videoTagRef.videoWidth,
                videoTagRef.videoHeight,
                videoTagRef.duration
              );
              // document.getElementById("uploadFilename").innerHTML =
              //   fileInformation.name;
              self.fileInformation.height = videoTagRef.videoHeight;
              self.fileInformation.width = videoTagRef.videoWidth;
              self.fileInformation.duration = videoTagRef.duration;
              self.notImage = false;
              self.notAudio = false;
              self.notVideo = false;
              self.dialogFormVisible = true;
              self.$swal.close(); //元数据加载完成后自动关闭对话框
            });
          };
        }
        //传的是音频
        else if (file.type === "audio/mp3") {
          console.log("mp3加载中");
          //等待视频数据加载弹框
          self.$swal({
            // toast: true,
            // position: 'top',
            showConfirmButton: false,
            allowOutsideClick: false, //点击背景不会关闭
            type: "info",
            // title: "文件正在处理中..."
            title: "<i class='el-icon-loading'></i>",
            text: "文件读取中..."
          });

          let reader = new FileReader();
          reader.readAsDataURL(file); //这里把一个文件用base64编码
          reader.onload = function(e) {
            let dataUrl = reader.result;
            let audioId = "videoMain";
            let $audioEl = $('<audio id="' + audioId + '"></audio>');
            // $("body").append($videoEl);
            $audioEl.attr("src", dataUrl);
            let audioTagRef = $audioEl[0];

            //媒体（音频、视频）相关事件loadstart、loadmetadata
            audioTagRef.addEventListener("loadedmetadata", function(e) {
              console.log(audioTagRef.duration);
              // document.getElementById("uploadFilename").innerHTML =
              //   fileInformation.name;
              self.fileInformation.height = 1;
              self.fileInformation.width = 1;
              self.fileInformation.duration = audioTagRef.duration;

              self.notImage = true;
              self.notVideo = true;
              self.dialogFormVisible = true;
              self.$swal.close(); //元数据加载完成后自动关闭对话框
            });
          };
        }
        // console.log(file);
        self.fileTitle = "文件名：" + file.name;
      }
      //"image/png,image/jpeg,audio/mpeg,audio/mp4, video/mp4"
      // showModal('styleContainer');
      //document.getElementById('uploadFilename').innerHTML = fileInformation.name;
      //showModal('styleContainer');
    },

