    //计算预期时间
    calculateEsTime: function(
      width,
      height,
      duration,
      time_us, //以微秒为单位的时间
      isFrame,
      frameObj
    ) {
      let time; //以秒为单位
      if (!isFrame) {
        //如果是图像
        if (duration === 0) {
          time = (width * height * time_us) / 1000000;
        }
        //否则是音频或视频
        else {
          time = (width * height * duration * time_us) / 1000000;
        }
        // document.getElementById('estimatedTime').innerHTML = "预计时间:" + self.formatDuration(time);
      } else {
        time =
          frameObj.frame_patch_rate *
            duration *
            29.9 *
            frameObj.estimated_time +
          (width *
            height *
            duration *
            (1 - frameObj.frame_patch_rate) *
            time_us) /
            1000000;
      }
      return time;
    },

    //格式化预期时间
    formatDuration: function(time) {
      time = Math.floor(time);
      if (time > -1) {
        var hour = Math.floor(time / 3600);
        var min = Math.floor(time / 60) % 60;
        var sec = time % 60;
        if (hour < 10) {
          time = "0" + hour + ":";
        } else {
          time = hour + ":";
        }

        if (min < 10) {
          time += "0";
        }
        time += min + ":";

        if (sec < 10) {
          time += "0";
        }
        time += sec;
      }
      return time;
    },

    // select变化时调用
    esTime: function() {
      //预计时间
      //与预计时间有关的有三个，图片，音频，是否补帧
      //补帧率（frame_patch_rate）x帧总数x补帧一张耗时+（1-补帧率）x帧总数x普通转换一张耗时+音频耗时
      this.fileInformation.estimated_time =
        this.calculateEsTime(
          1,
          1,
          this.fileInformation.duration,
          this.form.audioValue.aus_estimated_time
        ) + //音频耗时
        this.calculateEsTime(
          this.fileInformation.width,
          this.fileInformation.height,
          this.fileInformation.duration,
          // imageArray[0].ims_estimated_time,
          this.form.imageValue.ims_estimated_time,
          this.fileInformation.isFrame,
          this.styleResultData.data.patch_frame
        ); //图像耗时，默认时使用不补帧的

      console.log(this.fileInformation.estimated_time);
    },

