    //点击按钮创建任务发请求
    createTask: function() {
      // TODO:点击上传按钮
      let self = this;
      self.stepActive++;
      self.canBackLast = false;
      if (
        typeof self.form.imageValue.ims_id === "undefined" ||
        typeof self.form.audioValue.aus_id === "undefined"
      ) {
        self.$swal({
          type: "warning", // 弹框类型
          title: "未完整填写表单，请继续操作", //标题
          // text: "注销后将无法恢复，请谨慎操作！", //显示内容
          confirmButtonColor: "#3085d6", // 确定按钮的 颜色
          confirmButtonText: "确定", // 确定按钮的 文字
          showCancelButton: false // 是否显示取消按钮
        });
        self.stepActive--;
        self.canBackLast = true;
      } else {
        self
          .$swal({
            type: "warning", // 弹框类型
            title: "确认上传并开始转换？", //标题
            // text: "注销后将无法恢复，请谨慎操作！", //显示内容
            confirmButtonColor: "#3085d6", // 确定按钮的 颜色
            confirmButtonText: "确定", // 确定按钮的 文字
            showCancelButton: true, // 是否显示取消按钮
            cancelButtonColor: "#d33", // 取消按钮的 颜色
            cancelButtonText: "取消", // 取消按钮的 文字

            focusCancel: true, // 是否聚焦 取消按钮
            reverseButtons: true // 是否 反转 两个按钮的位置 默认是  左边 确定  右边 取消
          })
          .then(isConfirm => {
            try {
              //判断 是否 点击的 确定按钮
              if (isConfirm.value) {
                //self.$swal("成功", "点击了确定", "success");
                //做个判断，根据styleString传不同的数据
                let dataForm = new FormData();
                switch (self.fileInformation.styleString) {
                  case "video":
                    //TODO:
                    console.log(fileInformation.styleString);
                    dataForm.set("file", self.fileInformation.uploadFile);

                    // dataForm.append("user_id", userInfo.userId); //TODO:
                    dataForm.append("type", self.fileInformation.styleString);
                    dataForm.append(
                      "estimated_time",
                      self.fileInformation.estimated_time
                    );
                    dataForm.append("ims_id", self.form.imageValue.ims_id);
                    dataForm.append("aus_id", self.form.imageValue.aus_id);

                    dataForm.append("clarity", self.form.clarityValue);
                    dataForm.append(
                      "is_frame_speed",
                      self.fileInformation.isFrame
                    );
                    // console.log(dataForm);
                    $.ajax({
                      type: "POST",
                      url: "/task/create",
                      data: dataForm,
                      processData: false, // 注意：让jQuery不要处理数据
                      contentType: false // 注意：让jQuery不要设置contentType
                    }).done(function(resultData) {
                      let resultDataObj = resultData;
                      let resultErrcode = resultDataObj.error_code;
                      switch (resultErrcode) {
                        case 0:
                          //TODO:成功
                          self.$swal({
                            // toast: true,
                            // position: 'top',
                            showConfirmButton: true,
                            timer: 2000,
                            type: "success",
                            title: "已成功上传，请耐心等待处理"
                          });
                          break;
                      }
                    });
                    break;

                  case "audio":
                    //
                    dataForm.set("file", self.fileInformation.uploadFile);

                    // dataForm.append("user_id", userInfo.userId); //TODO:
                    dataForm.append("type", self.fileInformation.styleString);
                    dataForm.append(
                      "estimated_time",
                      self.fileInformation.estimated_time
                    );
                    dataForm.append("aus_id", self.form.imageValue.aus_id);

                    dataForm.append(
                      "is_frame_speed",
                      self.fileInformation.isFrame
                    );
                    $.ajax({
                      type: "POST",
                      url: "/task/create",
                      data: dataForm,
                      processData: false, // 注意：让jQuery不要处理数据
                      contentType: false // 注意：让jQuery不要设置contentType
                    }).done(function(resultData) {
                      let resultDataObj = resultData;

                      let resultErrcode = resultDataObj.error_code;
                      switch (resultErrcode) {
                        case 0:
                          //成功
                          self.$swal({
                            // toast: true,
                            // position: 'top',
                            showConfirmButton: true,
                            timer: 2000,
                            type: "success",
                            title: "已成功上传，请耐心等待处理"
                          });
                          break;
                      }
                    });
                    break;
                  case "image":
                    dataForm.set("file", self.fileInformation.uploadFile);

                    // dataForm.append("user_id", userInfo.userId); //TODO:
                    dataForm.append("type", self.fileInformation.styleString);
                    dataForm.append(
                      "estimated_time",
                      self.fileInformation.estimated_time
                    );
                    dataForm.append("ims_id", self.form.imageValue.ims_id);

                    dataForm.append("clarity", self.form.clarityValue);
                    $.ajax({
                      type: "POST",
                      url: "/task/create",
