    // 下面是控制拖拽上传的
    dragOver: function(ev) {
      ev.preventDefault();
      //拖入文件后边框颜色变化
      document.getElementById("dashboard").style.borderColor = "#ffdd59";
    },
    dragLeave: function() {
      document.getElementById("dashboard").style.borderColor =
        "rgb(124, 219, 248)";
    },
    dashBoardDrop: function(ev) {
      document.getElementById("dashboard").style.borderColor =
        "rgb(124, 219, 248)";
      ev.preventDefault();
      let self = this;
      if (!this.loginState) {
        this.$swal({
          // toast: true,
          // position: 'top',
          showConfirmButton: true, //确定按钮
          allowOutsideClick: false, //点击背景不会关闭
          timer: 3000,
          type: "warning",
          title: "您尚未登陆",
          onClose: function() {
            //弹窗关闭时调用的函数
            // showModal("login-box");
            self.$router.push({ name: "login" });
          }
        });
      } else {
        let files = ev.dataTransfer.files;
        // console.log(files);
        let file = files[0];
        console.log(file);
        //如果不合格式，重新选文件
        if (
          file.type !== "image/png" &&
          file.type !== "image/jpeg" &&
          file.type !== "audio/mp3" &&
          file.type !== "audio/mp4" &&
          file.type !== "video/mp4"
        ) {
          self.$swal({
            // toast: true,
            // position: 'top',
            showConfirmButton: true,
            allowOutsideClick: false, //点击背景不会关闭
            type: "warning",
            title: "不支持该格式",
            text: "仅支持mp4，jpg，jpeg，png，mp3,请重新选择"
          });
        } else if (file.size > 400 * 1000 * 1000) {
          self.$swal({
            // toast: true,
            // position: 'top',
            showConfirmButton: true,
            allowOutsideClick: false, //点击背景不会关闭
            type: "warning",
            title: "文件太大了",
            text: "请尝试上传小于400M的文件"
          });
        }
        //符合要求的格式
        else {
          self.fileInformation.name = file.name;
          self.fileInformation.type = file.type;
          self.fileInformation.uploadFile = file;
          console.log(self.fileInformation);
          //"image/png,image/jpeg,audio/mpeg,audio/mp4, video/mp4"
          //传的是图片
          if (file.type === "image/png" || file.type === "image/jpeg") {
            let reader = new FileReader();
            reader.readAsDataURL(file); //这里把一个文件用base64编码
            reader.onload = function(e) {
              let img = new Image();
              img.src = e.target.result; //获取编码后的值,也可以用this.result获取
              img.onload = function() {
                console.log(
                  "height:" + this.height + "----width:" + this.width
                );
                // document.getElementById("uploadFilename").innerHTML =
                //   fileInformation.name;
                self.fileInformation.height = this.height;
                self.fileInformation.width = this.width;
                self.fileInformation.duration = 0;
                self.notAudio = true;
                self.notVideo = true;
                self.dialogFormVisible = true;
              };
            };
          }
          //传的是视频
          else if (file.type === "video/mp4") {
            //等待视频数据加载弹框
            self.$swal({
              // toast: true,
              // position: 'top',
              showConfirmButton: false,
              allowOutsideClick: false, //点击背景不会关闭
              type: "info",
              title: "<i class='el-icon-loading'></i>",
              text: "文件读取中..."
            });

            let reader = new FileReader();
            reader.readAsDataURL(file); //这里把一个文件用base64编码
            reader.onload = function(e) {
              let dataUrl = reader.result;
              let videoId = "videoMain";
              let $videoEl = $('<video id="' + videoId + '"></video>');
              // $("body").append($videoEl);
              $videoEl.attr("src", dataUrl);
              let videoTagRef = $videoEl[0];

              //媒体（音频、视频）相关事件loadstart、loadmetadata
              //参考https://developer.mozilla.org/zh-CN/docs/Web/Guide/Events/Media_events

              videoTagRef.addEventListener("loadedmetadata", function(e) {
                console.log(
                  videoTagRef.videoWidth,
                  videoTagRef.videoHeight,
                  videoTagRef.duration
                );
                // document.getElementById("uploadFilename").innerHTML =
                //   fileInformation.name;
                self.fileInformation.height = videoTagRef.videoHeight;
                self.fileInformation.width = videoTagRef.videoWidth;
                self.fileInformation.duration = videoTagRef.duration;
                self.notImage = false;
                self.notAudio = false;
                self.notVideo = false;
                self.dialogFormVisible = true;
                self.$swal.close(); //元数据加载完成后自动关闭对话框
              });
            };
          }
          //传的是音频
          else if (file.type === "audio/mp3") {
            console.log("mp3加载中");
            //等待视频数据加载弹框
            self.$swal({
              // toast: true,
              // position: 'top',
              showConfirmButton: false,
              allowOutsideClick: false, //点击背景不会关闭
              type: "info",
              // title: "文件正在处理中..."
              title: "<i class='el-icon-loading'></i>",
              text: "文件读取中..."
            });

            let reader = new FileReader();
            reader.readAsDataURL(file); //这里把一个文件用base64编码
            reader.onload = function(e) {
              let dataUrl = reader.result;
              let audioId = "videoMain";
              let $audioEl = $('<audio id="' + audioId + '"></audio>');
              // $("body").append($videoEl);
              $audioEl.attr("src", dataUrl);
              let audioTagRef = $audioEl[0];

              //媒体（音频、视频）相关事件loadstart、loadmetadata
              audioTagRef.addEventListener("loadedmetadata", function(e) {
                console.log(audioTagRef.duration);
                // document.getElementById("uploadFilename").innerHTML =
                //   fileInformation.name;
                self.fileInformation.height = 1;
                self.fileInformation.width = 1;
                self.fileInformation.duration = audioTagRef.duration;

                self.notImage = true;
                self.notVideo = true;
                self.dialogFormVisible = true;
                self.$swal.close(); //元数据加载完成后自动关闭对话框
              });
            };
          }
          // console.log(file);
          self.fileTitle = "文件名：" + file.name;
        }
        //"image/png,image/jpeg,audio/mpeg,audio/mp4, video/mp4"
        // showModal('styleContainer');
        //document.getElementById('uploadFilename').innerHTML = fileInformation.name;
        //showModal('styleContainer');
      }
    }
  }
  // created: function() {
  //   console.log("createAPP");
  //   this.loginState=this.$parent.loginState;
  // },
};
</script>
