        </div>
        <!-- <div class="form-group">
          <label for="password">re-password</label>
          <input
            type="password"
            class="form-control"
            id="re-password"
            placeholder="请再次输入密码"
            autocomplete="off"
          />
        </div>-->
        <!-- <div class="form-group">
          <input type="checkbox" id="remember" />记住密码
        </div>-->
        <div class="form-group form-group-bottom">
          <input @click="register" type="button" value="注册" class="btn btn-primary" />
          <p class="form-group-display">
            已有账户?
            <router-link to="login">去登录</router-link>
          </p>
        </div>
      </form>
    </div>
  </div>
</template>

<style scoped>
