  font-family: "Open Sans", Arial, sans-serif;
  line-height: 1.5;
  font-size: 16px;
  color: #848484;
  background-color: #f0f0f0;
  height: 100%;
  overflow: auto;
  margin: 0;
}

/* 最外层的div */
.row {
  margin-left: -15px;
  margin-right: -15px;
}

/* 注册分区 */
.sign-up {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}

/* 分区内的表单 */
.outside-form {
  padding: 35px;
  margin-top: 60px; /*根据导航的高度定*/
  margin-left: 10%;
  margin-right: 10%;
  -webkit-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
  -o-box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
  box-shadow: -4px 7px 46px 2px rgba(0, 0, 0, 0.1);
  background: #ffffff;
  border-radius: 4px;
  margin-bottom: 50px;
}

