<script>
import $ from "jquery";
export default {
  data() {
    return {
      userName: "",
      password: ""
    };
  },
  methods: {
    toFront: function() {
      this.$router.push({
        name: "main",
        params: {
          // username: "LiHua",
          // userId: "2"
        }
      });
    },
    register: function() {
      let self = this;
      console.log("yes!register");
      // console.log($("input[id='l-username']").val() === "");
      //当输入值不等于""时才能进行提交
      if (this.userName !== "" && this.password !== "") {
        $.ajax({
          url: "/user/register",
          type: "POST",
          data: {
            userName: self.userName,
            passWord: self.password
          },
          // dataType:'',
          success: function(resultData) {
            // alert("请求成功");
            console.log(resultData);
            //TODO:因为用mock，直接那样格式是json，所以这里转换了一下，最后完成后不用转
            // var dataObj = JSON.parse(resultData); //将收到的数据转换成js对象
            let dataObj = resultData;
            // console.log(dataObj);
            // console.log(dataObj.error_code);
            // console.log(dataObj.data[1]);
            switch (dataObj.error_code) {
              case 0:
                self.$swal({
                  // toast: true,
                  // position: 'top',
                  showConfirmButton: false,
                  timer: 2000,
                  type: "success",
                  title: "您已经成功注册，可以去登陆了"
                });

                break;
              case -1:
                self.$swal({
                  // toast: true,
                  // position: 'top',
                  showConfirmButton: true,
                  timer: 2000,
                  type: "warning",
                  title: dataObj.error_msg
                });
                break;
              case -2:
                self.$swal({
                  // toast: true,
                  // position: 'top',
                  showConfirmButton: true,
                  timer: 2000,
                  type: "warning",
                  title: dataObj.error_msg
                });
                break;
              case -3:
                self.$swal({
                  // toast: true,
                  // position: 'top',
                  showConfirmButton: true,
                  timer: 2000,
                  type: "warning",
                  title: dataObj.error_msg
                });
                break;

              default:
                self.$swal({
                  // toast: true,
                  // position: 'top',
                  showConfirmButton: true,
                  timer: 2000,
                  type: "warning",
                  title: "接口返回值错误,请重试"
                });
                break;
            }
          },
          error: function() {
            self.$swal({
              // toast: true,
              // position: 'top',
              showConfirmButton: false,
              timer: 3000,
              type: "warning",
              title: "请求出错，请稍等"
            });
          }
        });
      } else if (self.userName === "") {
        self.$swal({
          toast: true,
          position: "top",
          showConfirmButton: false,
          timer: 3000,
          type: "warning",
          title: "您未完整填写用户名"
        });
      } else if (self.password === "") {
        self.$swal({
          toast: true,
          position: "top",
          showConfirmButton: false,
          timer: 3000,
          type: "warning",
          title: "您未完整填写密码"
        });
      }
    }
  }
};
</script>